import React, { useEffect, useState } from "react";
import moreStyle from "../../styles/more.module.css";
const More = ({ moreRef }) => {
  const [scrollVisible, setScrollVisible] = useState(false);
  const [scrollVisible2, setScrollVisible2] = useState(false);
  const [scrollVisible3, setScrollVisible3] = useState(false);
  const [scrollVisible4, setScrollVisible4] = useState(false);
  const [scrollVisible5, setScrollVisible5] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      const scrolled = window.scrollY;
      const offsetTop = moreRef.current.offsetTop;

      setScrollVisible(scrolled > offsetTop - window.innerHeight / 2);
      setScrollVisible2(scrolled > offsetTop - window.innerHeight / 3);
      setScrollVisible3(scrolled > offsetTop - window.innerHeight / 4);
      setScrollVisible4(scrolled > offsetTop - window.innerHeight / 5);
      setScrollVisible5(scrolled > offsetTop - window.innerHeight / 8);
    };

    window.addEventListener("scroll", handleScroll);

    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, [moreRef]);

  return (
    <div ref={moreRef} className={moreStyle.more}>
      <div className={moreStyle.flex}>
        <div
          className={`${moreStyle.title} ${
            scrollVisible ? moreStyle.fadeIn : ""
          }`}
        >
          <h1>  ,</h1>
          <hr
            className={`${moreStyle.line1} ${
              scrollVisible ? moreStyle.long : ""
            }`}
          />
        </div>
        <div className={moreStyle.txtBox}>
          <h2
            className={`${moreStyle.txt1} ${
              scrollVisible2 ? moreStyle.fadeIn : ""
            }`}
          >
              {" "}
            <strong className={moreStyle.strong1}>:</strong>
            <strong className={moreStyle.strong2}>)</strong>
          </h2>
          <h3
            className={`${moreStyle.txt2} ${
              scrollVisible3 ? moreStyle.fadeIn : ""
            }`}
          >
              
            <br />
               ,
          </h3>
          <h3
            className={`${moreStyle.txt3} ${
              scrollVisible4 ? moreStyle.fadeIn : ""
            }`}
          >
            <strong className={moreStyle.strong1}></strong>
            <strong className={moreStyle.strong2}></strong>
            <strong className={moreStyle.strong3}></strong>   
            <br />
             <strong className={moreStyle.strong1}></strong>
            <strong className={moreStyle.strong2}></strong> ,
          </h3>
          <div
            className={`${moreStyle.link} ${
              scrollVisible5 ? moreStyle.fadeIn : ""
            }`}
          >
            <h3 className={moreStyle.linkh3}>
                 , <span></span>
            </h3>
            <div className={moreStyle.flex2}>
              <a href="#" className={moreStyle.notion}>
                Notion, <span className={moreStyle.arrow}></span>
              </a>
            </div>
            <div className={moreStyle.flex2}>
              <a href="#" className={moreStyle.github}>
                GitHub, <span></span>
              </a>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default More;
